/* eslint-disable react-hooks/exhaustive-deps */
import React, { useCallback, useEffect, useRef, useState } from 'react';
import { useSwipeable } from 'react-swipeable';
import axios from 'axios';
import BannerAdHandler from './banner-ad/BannerAdHandler';
import './styles.css'
import { getConfig, includeScript } from './common/helper';
import JbRewardedAdHandler from './rewarded-ads/JbRewardedAdHandler';
import OverlayComponent from './ui-components/OverlayComponent';
import OfflineComponent from './ui-components/OfflineComponent';
import VideoList from './ui-components/VideoList';
import { container, controlOverlay, gradientOverlay, overlay, videoContainer, videoEmbed, videoWrapper } from './css';
import ShareButtons from './ui-components/ShareButtons';
import { API_KEY } from './config';

const PLAYLIST_ID = 'PLI56Ki8iIHgpBdxKIFubbO72wTYnM0TAx';

function App() {
  const [videoData, setVideoData] = useState([]);
  const [currentIndex, setCurrentIndex] = useState(null);
  const [loading, setLoading] = useState(false);
  const [fullAdVisible, setFullAdVisible] = useState(false);
  const [mute, setMute] = useState(false);
  const [openShare, setOpenShare] = useState(false);
  const playerRef = useRef(null);
  const ytPlayerRef = useRef(null);
  const videoSwipeCount = useRef(0);
  const bannerAdHandler = useRef(null);
  const rewardedAdHandler = useRef({
    adHandler: null
  });
  let bannerAdConfig = {
    "enabled": false,
    "adUnit": [
      {
        "id": "/6355419/Travel/Europe",
        "size": [300, 250]
      }
    ]
  }
  const [isOnline, setIsOnline] = useState(navigator.onLine);

  const loadRewardedAdScript = () => {
    includeScript({
      src: "https://mcm.justbaat.org/scripts/jb-rewarded-ad.min.js",
      async: true,
    });
  }

  useEffect(() => {
    const updateOnlineStatus = () => {
      setIsOnline(navigator.onLine);
    };

    window.addEventListener('online', updateOnlineStatus);
    window.addEventListener('offline', updateOnlineStatus);

    return () => {
      window.removeEventListener('online', updateOnlineStatus);
      window.removeEventListener('offline', updateOnlineStatus);
    };
  }, []);

  const handlers = useSwipeable({
    onSwipedUp: () => {
      videoSwipeCount.current += 1;
      changeVideo('next')
    },
    onSwipedDown: () => {
      videoSwipeCount.current -= 1;
      changeVideo('prev')
    },
    swipeDuration: 500,
    preventDefaultTouchmoveEvent: true,
    trackMouse: true,
  });

  const changeVideo = (direction) => {
    if (videoSwipeCount.current <= 0) {
      setCurrentIndex(0);
      videoSwipeCount.current = 0;
      return;
    }
    if (videoSwipeCount.current % 2 !== 0) {
      const iframeDiv = document?.getElementsByTagName('iFrame')[0]
      if (!iframeDiv?.id?.includes("google_ads")) {
        iframeDiv?.remove()
      }

      if (rewardedAdHandler?.current?.adHandler) {
        rewardedAdHandler?.current?.adHandler.renderAd({
          href: window.location.href
        });
        ytPlayerRef.current = null;
        setFullAdVisible(true)
      }
      const onAdGranted = (passbackConfig) => {
        if (passbackConfig?.href) {
          videoSwipeCount.current += 1
          changeVideo("next");
        }
      };

      const onAdRejected = (passbackConfig) => {
        videoSwipeCount.current += 1
        changeVideo("next")
      };

      rewardedAdHandler.current.adHandler.onAdGranted(onAdGranted);
      rewardedAdHandler.current.adHandler.onAdRejected(onAdRejected);
    } else if (currentIndex !== null) {
      setFullAdVisible(false)
      setMute(false)
      let newIndex = direction === 'next'
        ? (currentIndex + 1) % videoData.length
        : (currentIndex - 1 + videoData.length) % videoData.length;
      if (!isNaN(newIndex) && videoData.length > 0) {
        setCurrentIndex(newIndex);
      } else {
        console.warn("Index calculation resulted in NaN or videoData is empty");
      }
    }
  };

  useEffect(() => {
    const fetchPlaylistData = async () => {
      try {
        const response = await axios.get(
          `https://www.googleapis.com/youtube/v3/playlistItems?part=snippet&maxResults=50&playlistId=${PLAYLIST_ID}&key=${API_KEY}`
        );

        const videoIds = response.data.items.map(item => item.snippet.resourceId.videoId).join(',');

        const videoDetailsResponse = await axios.get(
          `https://www.googleapis.com/youtube/v3/videos?part=statistics,snippet&id=${videoIds}&key=${API_KEY}`
        );

        const items = videoDetailsResponse.data.items.map(item => ({
          id: item.id,
          src: `https://www.youtube.com/watch?v=${item.id}`,
          videoTitle: item.snippet.title,
          thumbnails: item.snippet.thumbnails.high.url,
          likes: item.statistics.likeCount > 1000 ? (item?.statistics?.likeCount / 1000)?.toFixed(1) + ' K' : item.statistics.likeCount
        }));

        setVideoData(items);
      } catch (error) {
        console.error('Failed to fetch playlist data', error);
      }
    };

    if (!rewardedAdHandler.current.adHandler) {
      rewardedAdHandler.current.adHandler = new JbRewardedAdHandler();
    }

    fetchPlaylistData();
    loadRewardedAdScript();
    getConfig();
  }, []);

  useEffect(() => {
    if (currentIndex !== null) {
      const videoId = videoData[currentIndex].id;

      const onYouTubeIframeAPIReady = () => {
        ytPlayerRef.current = new window.YT.Player(playerRef.current, {
          height: '100%',
          width: '100%',
          videoId: videoId,
          playerVars: {
            listType: 'playlist',
            list: PLAYLIST_ID,
            loop: 1,
            autoplay: 1,
            rel: 0,
            controls: 0,
            modestbranding: 1,
            showinfo: 0,
            autohide: 1,
            playsinline: 1,
            fs: 0,
            enablejsapi: 1,
            cc_load_policy: 3
          },
          events: {
            onReady: () => {
              if (document.getElementById('banner-parent')) {
                let g = document.createElement('div');
                g.id = 'banner-placement';
                document.getElementById('banner-parent')
                  .appendChild(g);
                if (!bannerAdHandler?.current) {
                  bannerAdHandler.current = new BannerAdHandler(
                    bannerAdConfig,
                    "banner_site_side"
                  );
                }
                bannerAdConfig.adPlacementId = g.id;
                if (bannerAdHandler?.current?.adSlot) {
                  bannerAdHandler?.current?.refreshSlot();
                } else {
                  bannerAdHandler?.current?.onReady(bannerAdHandler?.current?.defineAndDisplaySlot);
                }
              }
              setLoading(false)

            },
            onStateChange: handlePlayerStateChange,
          },
        });
      };

      if (window.YT) {
        onYouTubeIframeAPIReady();
      } else {
        loadYouTubeIframeAPI(onYouTubeIframeAPIReady);
      }
    }
  }, [currentIndex, videoData]);

  const loadYouTubeIframeAPI = (callback) => {
    const tag = document.createElement('script');
    tag.src = 'https://www.youtube.com/iframe_api';
    const firstScriptTag = document.getElementsByTagName('script')[0];
    firstScriptTag.parentNode.insertBefore(tag, firstScriptTag);

    window.onYouTubeIframeAPIReady = callback;
  };

  const handlePlayerStateChange = (event) => {
    console.log("Player state changed:", { ev: event.data, win: window.YT.PlayerState });
    if (ytPlayerRef.current) {
      if (event.data === window.YT.PlayerState.ENDED) {
        videoSwipeCount.current += 1
        changeVideo('next');
      } else if (event.data === window.YT.PlayerState.PAUSED) {
        ytPlayerRef.current?.mute();
        ytPlayerRef.current?.pauseVideo();
      } else if (event.data === window.YT.PlayerState.PLAYING) {
        ytPlayerRef.current?.unMute();
        ytPlayerRef.current?.playVideo();
        ytPlayerRef.current?.unloadModule("captions");
        ytPlayerRef.current?.unloadModule("cc");
      } else if (event.data === window.YT.PlayerState.UNSTARTED) {
        videoSwipeCount.current += 1
        changeVideo('next');
      }
    }
  };

  const togglePlayPause = () => {
    if (ytPlayerRef.current && typeof ytPlayerRef.current?.getPlayerState === "function") {
      const playerState = ytPlayerRef.current?.getPlayerState();
      if (playerState === window.YT.PlayerState.PLAYING) {
        ytPlayerRef.current.pauseVideo();
      } else {
        ytPlayerRef.current.playVideo();
      }
    }
  };

  const VideoView = useCallback(() => {
    if (!loading) {
      return (
        <div style={{
          ...videoContainer, height: window?.innerHeight
        }}>
          <div style={overlay} {...handlers}>
            <div style={{ ...videoEmbed }} id="video-player">
              <div ref={playerRef}></div>
            </div>
          </div>
        </div>
      );
    }
  }, [currentIndex, loading]);

  const toggleMuteUnmute = () => {
    setMute((prevMute) => {
      const newMuteState = !prevMute;
      if (ytPlayerRef?.current) {
        ytPlayerRef.current.muted = newMuteState;
        if (!newMuteState) {
          ytPlayerRef?.current?.unMute();
        }
        else {
          ytPlayerRef?.current?.mute();
        }
      }
      return newMuteState;
    });
  }

  const handleShareClick = () => {
    setOpenShare(prev => !prev)
  }

  const Overlay = useCallback(() => {
    if (!fullAdVisible) {
      return (
        <OverlayComponent
          handlers={handlers}
          togglePlayPause={togglePlayPause}
          mute={mute}
          toggleMuteUnmute={toggleMuteUnmute}
          handleShareClick={handleShareClick}
          videoData={videoData}
          currentIndex={currentIndex} />
      )
    }
  }, [fullAdVisible, mute, currentIndex])

  return (
    <div>
      {!isOnline ?
        <OfflineComponent />
        :
        currentIndex !== null ? (
          <div style={{
            ...videoWrapper, height: window?.innerHeight,
            touchAction: fullAdVisible ? null : 'none'
          }}>
            <VideoView />
            {!fullAdVisible ?
              <Overlay />
              :
              <div style={controlOverlay} {...handlers} />
            }
            <div id="banner-parent" style={{ ...gradientOverlay, zIndex: 9999, flexDirection: 'column', display: 'flex', justifyContent: 'center', color: '#fff', bottom: 0, paddingX: '2rem', paddingTop: '20px', whiteSpace: 'nowrap', textOverflow: 'ellipsis', background: 'linear-gradient(to top, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0.9), rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0))', alignItems: 'center' }} >
              <p style={{
                fontSize: '18px',
                fontWeight: 700,
                marginTop: '40px',
                overflow: 'hidden',
                textOverflow: 'ellipsis',
                whiteSpace: 'nowrap',
                width: `${window?.innerWidth - 40}px`,
                textAlign: 'center'
              }}>
                {videoData?.[currentIndex]?.videoTitle}
              </p>
            </div>
            {
              openShare &&
              <div style={{ height: '100%', background: '#000', zIndex: 9999 }}>
                <ShareButtons />
              </div>
            }
          </div>
        ) : (
          <div style={container}>
            <div style={{ display: 'flex', flexDirection: 'row' }} >
              {videoData.map((item, index) => (
                <VideoList videoData={item} index={index} setCurrentIndex={setCurrentIndex} />
              ))}
            </div>
          </div >
        )
      }
    </div >
  );
}

export default App;
