/* eslint-disable import/no-anonymous-default-export */
function getConstructor(input) {
    return input !== null && typeof input !== "undefined"
        ? input.constructor
        : null;
}

function instanceOf(input, constructor) {
    return Boolean(input && constructor && input instanceof constructor);
}

function nullOrUndefined(input) {
    return input === null || typeof input === "undefined";
}

function boolean(input) {
    return this.getConstructor(input) === Boolean;
}

function string(input) {
    return this.getConstructor(input) === String;
}

function number(input) {
    return this.getConstructor(input) === Number && !Number.isNaN(input);
}

function object(input) {
    return this.getConstructor(input) === Object;
}

function fn(input) {
    return typeof input === "function";
}

function array(input) {
    return Array.isArray(input);
}

function nodeList(input) {
    return this.instanceOf(input, NodeList);
}

function empty(input) {
    return (
        this.nullOrUndefined(input) ||
        ((this.string(input) || this.array(input) || this.nodeList(input)) &&
            !input.length) ||
        (this.object(input) && !Object.keys(input).length)
    );
}

export default {
    getConstructor,
    instanceOf,
    nullOrUndefined,
    boolean,
    string,
    number,
    object,
    fn,
    array,
    nodeList,
    empty,
};
