class BannerAdHandler {
    defaults = {
        adSizeMapping: {
            lg: {
                viewportSize: [1024, 768],
                slotSize: [
                    [970, 90],
                    [728, 90],
                    [320, 100],
                    [320, 50],
                    [300, 600],
                    [300, 250],
                    [300, 100],
                    [300, 50],
                    [250, 250],
                    [200, 200],
                ],
            },
            md: {
                viewportSize: [640, 0],
                slotSize: [
                    [320, 100],
                    [320, 50],
                    [300, 600],
                    [300, 250],
                    [300, 100],
                    [300, 50],
                    [250, 250],
                    [200, 200],
                ],
            },
            sm: {
                viewportSize: [320, 0],
                slotSize: [
                    [320, 100],
                    [320, 50],
                    [300, 250],
                    [300, 100],
                    [300, 50],
                    [250, 250],
                    [200, 200],
                ],
            },
            v_sm: {
                viewportSize: [0, 0],
                slotSize: [
                    [320, 50],
                    [300, 50],
                ],
            },
            v_md: {
                viewportSize: [0, 0],
                slotSize: [
                    [300, 250],
                    [250, 250],
                    [336, 280],
                    [320, 50],
                    [300, 50],
                ],
            },
            lg_site_side: {
                viewportSize: [0, 0],
                slotSize: [
                    [180, 500],
                    [160, 600],
                    [300, 600],
                    [300, 250]
                ],
            },
            banner_site_side: {
                viewportSize: [0, 0],
                slotSize: [
                    [320, 50],
                    [460, 68]
                    // [728, 90],
                    // [300, 600],
                    // [300, 250]
                ],
            },
        },
    };

    constructor(config, adSizeCategory = null) {
        this.config = config;
        this.adSizeCategory = adSizeCategory;
        this.scriptLoaded = false;

        this.adSlot = null;

        this.state = {};

        this.document = window.top.document;

        this.loadScript().then(() => {
            this.googletag = window.top.googletag;
            this.triggerReady();
            this.attachEventListeners();
        });
    }

    getTimeSecs() {
        return Math.floor(new Date().getTime() / 1000);
    }

    loadScript() {
        const topDoc = window.top.document;

        return new Promise((resolve) => {
            const src = "https://securepubads.g.doubleclick.net/tag/js/gpt.js";

            const existingScript = topDoc.querySelector(`script[src="${src}"]`);

            if (!existingScript) {
                const script = topDoc.createElement("script");
                script.src = src;
                script.async = true;

                script.onload = () => {
                    resolve();
                };

                topDoc.head.appendChild(script);
            } else {
                resolve();
            }
        });
    }

    defineSlot() {
        const sizeMappingBuilder = this.googletag.sizeMapping();

        if (this.adSizeCategory) {
            const mapping = this.defaults.adSizeMapping[this.adSizeCategory];
            sizeMappingBuilder.addSize(mapping.viewportSize, mapping.slotSize);
        } else {
            for (const key in this.defaults.adSizeMapping) {
                if (this.defaults.adSizeMapping.hasOwnProperty(key)) {
                    const mapping = this.defaults.adSizeMapping[key];
                    sizeMappingBuilder.addSize(
                        mapping.viewportSize,
                        mapping.slotSize
                    );
                }
            }
        }

        const sizeMapping = sizeMappingBuilder.build();

        console.log("this.config.adUnit[0].id,", {
            1: this.config.adUnit[0].id,
            2: this.config.adUnit[0].size,
            3: this.config.adPlacementId
        });
        return this.googletag
            .defineSlot(
                this.config.adUnit[0].id,
                this.config.adUnit[0].size,
                this.config.adPlacementId
            )
            .defineSizeMapping(sizeMapping);

    }

    defineAndDisplaySlot() {
        if (this.adSlot) return;

        this.googletag.cmd.push(() => {
            this.adSlot = this.defineSlot();

            this.adSlot.addService(this.googletag.pubads());

            this.googletag.enableServices();
            this.googletag.display(this.adSlot);

            // this.state.lastRenderedAttempt = getTimeSecs();
        });
        console.log("this.googletag.cmd", this.googletag.cmd)
    }

    refreshSlot() {
        if (!this.adSlot) return;

        this.googletag.cmd.push(() => {
            this.googletag.pubads().refresh([this.adSlot]);
        });
    }

    clearSlot() {
        if (!this.adSlot) return;

        this.googletag.cmd.push(() => {
            this.googletag.pubads().clear([this.adSlot]);
        });
    }

    destroySlot() {
        if (!this.adSlot) return;

        this.googletag.cmd.push(() => {
            this.googletag.destroySlots([this.adSlot]);
        });
    }

    onAdLoaded(callback) {
        if (typeof callback === "function") {
            this.adLoadedCallback = callback;
        } else {
            console.warn("onAdLoaded expects a function");
        }
    }

    onAdResponse(callback) {
        if (typeof callback === "function") {
            this.adResponseCallback = callback;
        } else {
            console.warn("onAdResponse expects a function");
        }
    }

    onReady(cb) {
        if (cb && typeof cb === "function") {
            this.onReadyCallback = cb;
        }
    }

    triggerReady() {
        if (
            this.onReadyCallback &&
            typeof this.onReadyCallback === "function"
        ) {
            this.onReadyCallback();
        }
    }

    attachEventListeners() {
        this.googletag.cmd.push(() => {
            this.googletag
                .pubads()
                .addEventListener("slotRenderEnded", (event) => {
                    if (event.slot === this.adSlot && event.isEmpty === false) {
                        if (
                            this.adLoadedCallback &&
                            typeof this.adLoadedCallback === "function"
                        ) {
                            this.adLoadedCallback();
                        }
                    }
                });

            this.googletag
                .pubads()
                .addEventListener("slotResponseReceived", (event) => {
                    if (event.slot === this.adSlot && !event.slot.getResponseInformation()) {
                        if (
                            this.adResponseCallback &&
                            typeof this.adResponseCallback === "function"
                        ) {
                            this.adResponseCallback();
                        }
                    }
                });
        });
    }
}

export default BannerAdHandler;
