import { generateUniqueId, getDomainName, includeScript, mergeObjects } from "../common/helper";

class RewardedAd {
    constructor(adUnitPath, grantedCallback, rejectedCallback) {
        if (!adUnitPath) {
            throw new Error("Ad unit path is required.");
        }

        this.googletag = window.googletag || { cmd: [] };

        this.adUnitPath = adUnitPath;
        this.grantedCallback = grantedCallback;
        this.rejectedCallback = rejectedCallback;

        this.boundRewardedSlotReady = this.onRewardedSlotReady.bind(this);
        this.boundRewardedSlotClosed = this.dismissRewardedAd.bind(this, true);
        this.boundRewardedSlotGranted = this.onRewardedSlotGranted.bind(this);
        this.boundRewardedSlotUnfilled = this.onRewardedSlotUnfilled.bind(this);
    }

    initAd() {
        this.googletag.cmd.push(() => {
            try {
                this.rewardedSlot = this.googletag.defineOutOfPageSlot(
                    this.adUnitPath,
                    this.googletag.enums.OutOfPageFormat.REWARDED
                );

                if (!this.rewardedSlot) {
                    console.error("Failed to define out-of-page slot.");
                    return;
                }

                this.rewardedSlot.addService(this.googletag.pubads());

                this.setupAdEvents();

                this.googletag.enableServices();
                this.googletag.display(this.rewardedSlot);
                if (this.googletag.pubads().isInitialLoadDisabled()) {
                    this.googletag.pubads().refresh([this.rewardedSlot]);
                }
            } catch (error) {
                console.error("Error initializing ad: ", error);
            }
        });
    }

    setupAdEvents() {
        if (!this.rewardedSlot) return;

        this.googletag
            .pubads()
            .addEventListener("rewardedSlotReady", this.boundRewardedSlotReady);
        this.googletag
            .pubads()
            .addEventListener(
                "rewardedSlotClosed",
                this.boundRewardedSlotClosed
            );
        this.googletag
            .pubads()
            .addEventListener(
                "rewardedSlotGranted",
                this.boundRewardedSlotGranted
            );

        this.googletag
            .pubads()
            .addEventListener(
                "slotRenderEnded",
                this.boundRewardedSlotUnfilled
            );
    }

    removeAdEvents() {
        if (!this.rewardedSlot) return;

        this.googletag
            .pubads()
            .removeEventListener(
                "rewardedSlotReady",
                this.boundRewardedSlotReady
            );
        this.googletag
            .pubads()
            .removeEventListener(
                "rewardedSlotClosed",
                this.boundRewardedSlotClosed
            );
        this.googletag
            .pubads()
            .removeEventListener(
                "rewardedSlotGranted",
                this.boundRewardedSlotGranted
            );

        this.googletag
            .pubads()
            .removeEventListener(
                "slotRenderEnded",
                this.boundRewardedSlotUnfilled
            );
    }

    onRewardedSlotReady(event) {
        event.makeRewardedVisible();
    }

    onRewardedSlotGranted() {
        this.dismissRewardedAd(false);

        if (typeof this.grantedCallback === "function") {
            this.grantedCallback();
        }
    }

    onRewardedSlotUnfilled(event) {
        if (event.slot === this.rewardedSlot && event.isEmpty) {
            this.grantedCallback();
        }
    }

    dismissRewardedAd(callback = true) {
        this.removeAdEvents();

        if (this.rewardedSlot) {
            this.googletag.destroySlots([this.rewardedSlot]);
        }

        if (callback && typeof this.rejectedCallback === "function") {
            this.rejectedCallback();
        }
    }

    render() {
        this.initAd();
    }
}

class JbAdModalRenderer {
    constructor(config, triggerAdGranted, triggerAdRejected) {
        this.config = config;
        this.triggerAdGranted = triggerAdGranted;
        this.triggerAdRejected = triggerAdRejected;

        this.initialize();
    }

    initialize() {
        this.config.ids = {
            adRenderBtnId: `jb-rewd__ad-render-btn__${this.config.instanceId}`,
            modalCloseBtnId: `jb-rewd__modal-close-btn__${this.config.instanceId}`,
        };

        this.addFontFamily();
        this.addStylesToDocument();
    }

    addFontFamily() {
        var head = document.getElementsByTagName("head")[0];

        var link = document.createElement("link");

        link.rel = "stylesheet";
        link.href =
            "https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap";

        link.onload = () => {
            var jbRewdModalDivs = document.querySelectorAll(
                "div.jb-rewd__modal *"
            );
            jbRewdModalDivs.forEach(function (el) {
                el.style.fontFamily = "Roboto, sans-serif";
            });
        };

        head.appendChild(link);
    }

    addStylesToDocument() {
        const cssString = `
        @keyframes jbFadeIn {
            from {opacity: 0;}
            to {opacity: 1;}
        }

        .jb-rewd__modal {
            display: none;
            position: fixed;
            z-index: 999999;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            overflow: auto;
            background-color: rgba(0,0,0,0.5);
            backdrop-filter: blur(3px);
            animation: jbFadeIn 0.2s;
        }

        .jb-rewd__modal-wrapper {
            width: 100%;
            height: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
        }

        .jb-rewd__modal-content {
            text-align: center;
            position: relative;
            width: 300px;
            min-height: 400px;
            display: flex;
            flex-flow: column wrap;
            place-content: center;
            align-items: center;
            gap: 24px;
            background-color: rgb(10, 20, 78);
            color: rgb(255, 255, 255);
            border-radius: 20px;
            padding: 44px 10px;
            border: 2px solid rgb(255, 255, 255);
        }
        
        .jb-rewd__dwld-btn {
            padding: 16px 25px;
            background-image: linear-gradient(#ffdb00, #ffdb00, #776c18 103%);
            border-radius: 50px;
            color: #255d9c;
            font-size: 26px;
            font-weight: 700;
            border: none;
            box-shadow: 2px 4px 10px 2px #17191b;
            cursor: pointer;
        }

        .jb-rewd__modal-close-btn {
            text-align: center;
            text-decoration: underline;
            color: #8592ff;
            cursor: pointer;
            font-size: 20px;
        }
        `;

        const style = document.createElement("style");
        // style.type = 'text/css';
        style.appendChild(document.createTextNode(cssString));
        document.head.appendChild(style);
    }

    createModal() {
        const modal = document.createElement("div");

        modal.id = `jb-rewd__modal__${this.config.instanceId}`;
        modal.classList.add("jb-rewd__modal");

        modal.innerHTML = this.getModalContentHtml();

        document.body.appendChild(modal);

        this.addEventListeners();

        this.modal = modal;
    }

    getModalContentHtml() {
        return `
            <div class="jb-rewd__modal-wrapper">
                <div class="jb-rewd__modal-content">
                    ${this.config?.modalUI?.imageUrl
                ? `<img width="120" height="120" src=${this.config?.modalUI?.imageUrl} alt="modal_hero_image">`
                : ""
            }
                    
                    <span style="font-size: 29px; font-family: inherit; text-align: center;">
                        ${this.config.modalUI.title}
                    </span>

                    <div style="border-radius: 2px;">
                        <p style="width: 100%; height: 18px; margin: 0; text-align: end; color: #ffdb00;">
                            Ad
                        </p>
                        <button id="${this.config.ids.adRenderBtnId
            }" class="jb-rewd__dwld-btn">
                            ${this.config.modalUI.showAdBtnText}
                        </button>
                    </div>

                    <span id="${this.config.ids.modalCloseBtnId
            }" class="jb-rewd__modal-close-btn">
                        Close
                    </span>

                    <span class="ourPowerOf">Powered By 
                        <span style="color: #66d4ff;">Justbaat.ai</span>
                    </span>
                </div>
            </div>`;
    }

    addEventListeners() {
        const activatorBtn = document.getElementById(
            this.config.ids.adRenderBtnId
        );
        activatorBtn.addEventListener("click", this.renderAd.bind(this));

        const modalCloseBtn = document.getElementById(
            this.config.ids.modalCloseBtnId
        );
        modalCloseBtn.addEventListener("click", this.destroyModal.bind(this));
    }

    renderAd() {
        console.log("this.config.adUnit", this.config.adUnit);
        this.rewardedAd = new RewardedAd(
            this.config.adUnit[0].id,
            this.triggerAdGranted.bind(this),
            this.triggerAdRejected.bind(this)
        );

        if (this.rewardedAd) {
            this.rewardedAd.render();

            this.destroyModal();
        } else {
            console.error("RewardedAdModal is not initialized.");
        }
    }

    removeEventListeners() {
        const activatorBtn = document.getElementById(this.config.activatorId);

        activatorBtn.removeEventListener("click", this.showModal);
    }

    showModal() {
        this.createModal();
        this.modal.style.display = "block";
    }

    destroyModal() {
        if (this.modal && this.modal.parentNode) {
            this.modal.parentNode.removeChild(this.modal);
        }
    }

    render() {
        this.showModal();
    }
}

class JbRewardedAdHandler {
    constructor(config = null) {
        this.defaultConfig = {
            hostActivatorBtn: {
                enabled: false,
                text: "View Rewarded Ad",
            },
            modalUI: {
                enabled: true,
                imageUrl: "https://reports.magicbid.ai/img/cloud-computing.png",
                title: "Unlock a free download by watching an ad",
                showAdBtnText: "Continue",
            },
            adUnit: [
                {
                    id: "/22639388115/rewarded_web_example",
                },
            ],
            CTA: {
                onSuccess: {
                    action: "REDIRECT",
                    redirectUrl: "https://google.com",
                    target: "_blank",
                },
                onReject: {
                    action: "REDIRECT",
                    redirectUrl: "",
                },
            },
        };

        this.onPluginReadyCallbacks = [];
        this.initialize(config);
    }

    async getConfig() {
        try {
            const hostname = getDomainName(false);

            const configUrl =
                hostname === "localhost"
                    ? "https://localhost:8080/rewarded-ad/rewarded-ad-config.json"
                    : `https://jbmcm.blob.core.windows.net/publishers/${hostname}/rewarded-ad-config.json`;

            let response = null;

            try {
                response = await fetch(configUrl);

                if (!response.ok) {
                    throw new Error(`HTTP error! status: ${response.status} at primary URL`);
                }
            } catch (error) {
                const cmpHostname = window.location.hostname.replace(/\./g, "_");
                const cmpConfigUrl = `https://jbmcm.blob.core.windows.net/publishers/${cmpHostname}/rewarded-ad-config.json`;

                try {
                    response = await fetch(cmpConfigUrl);
                    if (!response.ok) {
                        throw new Error(`HTTP error! status: ${response.status} at backup URL`);
                    }
                } catch (backupError) {
                    throw backupError;
                }
            }

            if (!response.ok) {
                throw new Error(`HTTP error! status: ${response.status}`);
            }

            this.config = await response.json();

            this.config.hostname = hostname;
        } catch (error) {
            console.error("JB: Error fetching config", error);
        }
    }

    async setupAd() {
        await includeScript({
            src: "https://securepubads.g.doubleclick.net/tag/js/gpt.js",
            async: true,
        });

        if (this.config?.modalUI?.enabled) {
            this.adModalRenderer = new JbAdModalRenderer(
                this.config,
                this.triggerAdGranted.bind(this),
                this.triggerAdRejected.bind(this)
            );
        } else {
            this.rewardedAd = new RewardedAd(
                this.config.adUnit[0].id,
                this.triggerAdGranted.bind(this),
                this.triggerAdRejected.bind(this)
            );
        }
    }

    async initialize(config) {
        try {
            if (config) {
                this.config = config;
            } else {
                await this.getConfig();
            }

            if (!this.config) {
                return;
            }

            this.config = mergeObjects(this.defaultConfig, this.config);

            this.config.instanceId = generateUniqueId(8);

            await this.setupAd();

            this.pluginReady = true;
            this.triggerPluginReady();
        } catch (error) {
            console.error("JB: Error initializing Rewarded Ads", error);
        }
    }

    renderAd(config = null) {
        if (config) {
            this.renderPassbackConfig = config;
        }

        if (this.config.modalUI.enabled) {
            if (this.adModalRenderer) {
                this.adModalRenderer.render();
            } else {
                console.error("RewardedAdModal is not initialized.");
            }
        } else {
            if (this.rewardedAd) {
                this.rewardedAd.render();
            } else {
                console.error("RewardedAd is not initialized.");
            }
        }
    }

    onAdGranted(callback) {
        this.onAdGrantedCallback = callback;
    }

    onAdRejected(callback) {
        this.onAdRejectedCallback = callback;
    }

    onReady(callback) {
        if (this.pluginReady) {
            callback();
        } else {
            this.onPluginReadyCallbacks.push(callback);
        }
    }

    triggerAdGranted() {
        if (typeof this.onAdGrantedCallback === "function") {
            this.onAdGrantedCallback(this.renderPassbackConfig);
        }
    }

    triggerAdRejected() {
        if (typeof this.onAdRejectedCallback === "function") {
            this.onAdRejectedCallback(this.renderPassbackConfig);
        }
    }

    triggerPluginReady() {
        this.onPluginReadyCallbacks.forEach((callback) => {
            if (typeof callback === "function") {
                callback();
            }
        });

        this.onPluginReadyCallbacks = [];
    }
}

window.JbRewardedAdHandler = JbRewardedAdHandler;
export default JbRewardedAdHandler;
