import is from "../is";

export function getDomainName(useRemoteDefault = true) {
    // let domain = getCanonicalDomain();

    const match = window.location.hostname.match(
        /^(?:https?:\/\/)?(?:[^@\n]+@)?(?:www\.)?([^\/\n?]+)$/i
    );

    const fullDomain = match && match[1];
    const parts = fullDomain.split(".");

    // Removing the TLD
    parts.pop();

    let hostname;
    let subdomain;

    if (parts.includes("localhost") || parts.length === 0) {
        hostname = useRemoteDefault ? "worldnewscorp" : "localhost";
    } else {
        if (parts.length > 2) {
            subdomain = parts[0];
            hostname = parts.slice(1).join("_");
            hostname = `${subdomain}_${hostname}`;
        } else {
            hostname = parts.join("_");
        }
    }

    return hostname;
}

export const getConfig = async () => {
    try {
        const hostname = getDomainName(false);

        const configUrl =
            hostname === "localhost"
                ? "https://localhost:3000/rewarded-ads/rewarded-ad-config.json"
                : `https://jbmcm.blob.core.windows.net/publishers/${hostname}/rewarded-ad-config.json`;

        let response = null;

        try {
            response = await fetch(configUrl);

            if (!response.ok) {
                throw new Error(`HTTP error! status: ${response.status} at primary URL`);
            }
        } catch (error) {
            const cmpHostname = window.location.hostname.replace(/\./g, "_");
            const cmpConfigUrl = `https://jbmcm.blob.core.windows.net/publishers/${cmpHostname}/rewarded-ad-config.json`;

            try {
                response = await fetch(cmpConfigUrl);
                if (!response.ok) {
                    throw new Error(`HTTP error! status: ${response.status} at backup URL`);
                }
            } catch (backupError) {
                throw backupError;
            }
        }

        if (!response.ok) {
            throw new Error(`HTTP error! status: ${response.status}`);
        }

        let config = await response.json();
        console.log("🚀 ~ getConfig ~ config:", config)

        config.hostname = hostname;

    } catch (error) {
        console.error("JB: Error fetching config", error);
    }
}

export function generateUniqueId(max = 8) {
    const characters =
        "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
    let uniqueId = "";

    for (let i = 0; i < max; i++) {
        const randomIndex = Math.floor(Math.random() * characters.length);
        uniqueId += characters.charAt(randomIndex);
    }

    return uniqueId;
}

/**
 * Fetches JSON data from a remote URL using async/await.
 * @param {string} url - The URL to fetch the JSON from.
 * @param {object} options - Optional parameters for fetch request.
 * @returns {Promise<any>} A promise that resolves with the JSON data.
 */
export async function fetchJson(url, options = {}) {
    if (typeof url !== "string" || !url) {
        throw new Error("URL must be a valid string.");
    }

    const defaultOptions = {
        method: "GET",
        headers: { "Content-Type": "application/json" },
    };

    const finalOptions = { ...defaultOptions, ...options };

    try {
        const response = await fetch(url, finalOptions);

        if (!response.ok) {
            throw new Error(`HTTP error! status: ${response.status}`);
        }

        const data = await response.json();

        return data;
    } catch (error) {
        console.error("Error fetching JSON:", error);
        throw error;
    }
}

export function includeStyles(style, appendor) {
    if (!style || !appendor) return;

    const styleTag = document.createElement("style");
    styleTag.type = "text/css";

    styleTag.appendChild(document.createTextNode(style));

    appendor.appendChild(styleTag);
}

export function includeScript(options, appendor = document) {
    return new Promise((resolve, reject) => {
        const existingScript = appendor.querySelector(
            `script[src="${options.src}"]`
        );

        if (existingScript) {
            console.log('JB_UM script already exists');
            if (options.cb && is.fn(options.cb)) {
                options.cb();
            }
            resolve();

            return;
        }

        const script = appendor.createElement("script");
        script.type = "text/javascript";
        script.src = options.src;

        if (options.async) {
            script.async = true;
        }
        if (options.defer) {
            script.defer = true;
        }

        if (options.attributes) {
            Object.keys(options.attributes).forEach((key) => {
                script.setAttribute(key, options.attributes[key]);
            });
        }

        script.onload = () => {
            if (options.cb && is.fn(options.cb)) {
                options.cb();
            }
            resolve();
        };

        script.onerror = () =>
            reject(new Error(`Script load error for ${options.src}`));

        // Append the script to the document
        appendor.head.appendChild(script);
    });
}

export function debounce(func, wait) {
    let timeout;

    return function (...args) {
        const later = () => {
            clearTimeout(timeout);

            func.apply(this, args);
        };

        clearTimeout(timeout);

        timeout = setTimeout(later, wait);
    };
}

export function shuffleArray(arr) {
    const groupedByPriority = arr.reduce((acc, item) => {
        const priority = item.priority || "JB1";

        if (!acc[priority]) {
            acc[priority] = [];
        }

        acc[priority].push(item);

        return acc;
    }, {});

    for (const priority in groupedByPriority) {
        const group = groupedByPriority[priority];

        for (let i = group.length - 1; i > 0; i--) {
            const j = Math.floor(Math.random() * (i + 1));
            [group[i], group[j]] = [group[j], group[i]];
        }
    }

    const sortedPriorities = Object.keys(groupedByPriority).sort((a, b) =>
        a.localeCompare(b, undefined, { numeric: true })
    );

    const shuffledArray = sortedPriorities.reduce(
        (acc, priority) => acc.concat(groupedByPriority[priority]),
        []
    );

    return shuffledArray;
}

export function chooseWithProbability(opt1, opt2, probability = 0.5) {
    if (Math.random() <= probability) {
        return opt1;
    } else {
        return opt2;
    }
}

export function detectDeviceType() {
    const ua = navigator.userAgent;

    if (
        /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(
            ua
        )
    ) {
        return "mobile";
    }

    if (
        ua.indexOf("iPad") !== -1 ||
        (ua.indexOf("Android") !== -1 && ua.indexOf("Mobile") === -1)
    ) {
        return "tablet";
    }

    const hasTouchScreen =
        "ontouchstart" in window ||
        navigator.maxTouchPoints > 0 ||
        navigator.msMaxTouchPoints > 0;

    if (hasTouchScreen) {
        // This could be a touch-capable laptop, but we might treat it as a 'mobile' for simplicity
        return "mobile";
    }

    return "desktop";
}

export function getNetworkSpeed() {
    if (!("connection" in navigator)) {
        return "standard";
    }

    let networkSpeed = navigator.connection.downlink;

    if (networkSpeed < 1.5) {
        return "low";
    } else if (networkSpeed <= 3) {
        return "medium";
    } else {
        return "high";
    }
}

export function getCanonicalDomain() {
    let hostOrigin = "";

    if (document.referrer) {
        const referrerUrl = new URL(document.referrer);
        hostOrigin = referrerUrl.origin;
    }

    if (
        window.location.ancestorOrigins &&
        window.location.ancestorOrigins.length > 0
    ) {
        hostOrigin =
            window.location.ancestorOrigins[
            window.location.ancestorOrigins.length - 1
            ];
    }

    return hostOrigin;
}

export function getScreenSize() {
    let effectiveWindow = window;

    if (window !== window.top) {
        //Rendered inside iframe
        effectiveWindow = window.top;
    }
    const screenWidth = effectiveWindow.innerWidth;

    if (screenWidth >= 992) {
        return "lg";
    } else if (screenWidth >= 768) {
        return "md";
    } else {
        return "sm";
    }
}

export function isElementInViewport(el) {
    var rect = el.getBoundingClientRect();

    return (
        rect.top >= 0 &&
        rect.left >= 0 &&
        rect.bottom <=
        (window.innerHeight || document.documentElement.clientHeight) &&
        rect.right <=
        (window.innerWidth || document.documentElement.clientWidth)
    );
}

export function getTimeSecs() {
    return Math.floor(new Date().getTime() / 1000);
}

export function mergeObjects(defaultObj, partnerObj) {
    const ignoreKeys = ["__comment"];

    const finalObj = defaultObj;

    for (const key in finalObj) {
        if (finalObj.hasOwnProperty(key)) {
            if (ignoreKeys.includes(key)) {
                delete finalObj[key];
            }

            if (
                partnerObj.hasOwnProperty(key) &&
                partnerObj[key] !== null &&
                partnerObj[key] !== undefined
            ) {
                switch (true) {
                    case is.object(partnerObj[key]):
                        mergeObjects(finalObj[key], partnerObj[key]);

                        break;

                    default:
                        finalObj[key] = is.empty(partnerObj[key])
                            ? finalObj[key]
                            : partnerObj[key];

                        break;
                }
            }
        }
    }

    return finalObj;
}

const CACHE_EXPIRATION_MS = 15 * 60 * 1000; // 15 minutes

export async function checkPathInCache(path, cacheType = 'jb-br-cache') {
    const cache = await caches.open(cacheType);
    let response = await cache.match(path);

    if (response) {
        const cachedTime = parseInt(response.headers.get('X-Cached-Time'), 10);
        const currentTime = Date.now();
        const isExpired = currentTime - cachedTime > CACHE_EXPIRATION_MS;

        if (isExpired) {
            console.log('Cache expired for path', path);
            response = null;
        }
    }

    if (!response) {
        console.log('Fetching and caching path', path);
        response = await fetch(path);
        if (!response.ok) {
            return response;
        }
        const clonedResponse = response.clone();

        const headers = new Headers(clonedResponse.headers);
        headers.append('X-Cached-Time', Date.now().toString());

        const cacheResponse = new Response(clonedResponse.body, {
            status: clonedResponse.status,
            statusText: clonedResponse.statusText,
            headers: headers
        });

        await cache.put(path, cacheResponse);
    }

    return response;
}

export async function fetchConfig(endpoint, localPath = "", textRes = false) {
    const baseConfigResourcePath =
        "https://jbmcm.blob.core.windows.net/publishers";

    const domainName = getFullDomainName();

    let remoteConfigUrl;

    if (domainName.includes("localhost")) {
        remoteConfigUrl = `https://localhost:8080/${localPath}/${endpoint}`;
    } else {
        remoteConfigUrl = `${baseConfigResourcePath}/${domainName}/${endpoint}`;
    }

    try {
        let response;
        if (domainName.includes("localhost")) {
            response = await fetch(remoteConfigUrl);
        } else {
            response = await checkPathInCache(remoteConfigUrl)
        }

        if (!response?.ok) {
            throw new Error(
                `Network response was not ok: ${response.statusText}`
            );
        }

        let data;

        if (textRes) {
            data = await response.text();
            data.domainName = domainName;
        } else {
            data = await response.json();
        }

        return data;
    } catch (error) {
        let config = null;

        function getCurrentWindow() {
            let currWindow = window;

            if (currWindow !== window.top) {
                currWindow = window.top;
            }

            return currWindow;
        }

        const currWindow = getCurrentWindow();

        const match = currWindow.location.hostname.match(
            /^(?:https?:\/\/)?(?:[^@\n]+@)?(?:www\.)?([^\/\n?]+)$/i
        );
        const hostname = match && match[1];

        const cmpHostname = hostname.replace(/\./g, "_");
        const cmpConfigUrl = `${baseConfigResourcePath}/${cmpHostname}/${endpoint}`;

        try {
            let response;
            if (domainName.includes("localhost")) {
                response = await fetch(cmpConfigUrl);
            } else {
                response = await checkPathInCache(cmpConfigUrl)
            }
            if (!response?.ok) {
                throw new Error(
                    `HTTP error! status: ${response.status} at backup URL`
                );
            }

            if (textRes) {
                config = await response.text();
            } else {
                config = await response.json();
            }
        } catch (backupError) {
            console.error(backupError);
        }

        return config;
    }
}

export function getFullDomainName() {
    const topWindow = window !== window.top ? window.top : window;

    const match = topWindow.location.hostname.match(
        /^(?:https?:\/\/)?(?:[^@\n]+@)?(?:www\.)?([^\/\n?]+)$/i
    );
    const hostname = match && match[1];

    const cmpHostname = hostname.replace(/\./g, "_");

    return cmpHostname || null;
}

export function checkKeyInArray(key, value, array) {
    return array.some(obj => {
        if (obj.hasOwnProperty(key)) {
            return obj[key] === value;
        }
        return false;
    })
}

export function getMaxAdSize(arr) {
    let maxSize = [0, 0];

    arr.forEach(obj => {
        if (obj.width >= maxSize[0] && obj.height >= maxSize[1]) {
            maxSize = [obj.width, obj.height];
        }
    });

    return maxSize;
}
